.TextCard{height:100%;padding-top:3rem;display:flex;flex-direction:column;justify-content:space-between}.TextCard .Top-TextCard{margin-bottom:1rem}.TextCard .Top-TextCard .Title-TextCard{display:flex;justify-content:center}.TextCard .Top-TextCard .Title-TextCard div{text-transform:uppercase;font-size:1.5rem;border-bottom:1px solid rgba(0,0,0,0);transition:border-bottom .5s}.TextCard .Top-TextCard .Title-TextCard div:hover{cursor:pointer;border-bottom:1px solid #000}.TextCard .Top-TextCard .Quote-TextCard{text-align:justify;line-height:145%;padding-top:20px}.TextCard .Top-TextCard .Quote-TextCard:before{content:"“";font-size:300%}.TextCard .Bottom-TextCard{display:flex;justify-content:flex-end;align-items:flex-end}.TextCard .Bottom-TextCard .Book-TextCard{border:1px solid #d6d6d6}.TextCard .Bottom-TextCard .Book-TextCard img{width:100px}.TextCard .Bottom-TextCard .Author-TextCard{max-width:180px;text-align:right;color:gray;margin-right:1rem;font-size:90%;line-height:120%}.TextCard .Bottom-TextCard .Author-TextCard:before{content:"—";margin-right:.5rem}