.weather-wrap{display:flex;justify-content:center;align-items:flex-start;font-size:70%;flex-direction:column}.weatherTitle{font-size:90%;color:#727272}.weatherInfo{display:flex;align-items:center}.weatherInfo .weather-icon{padding-right:20px}.weatherInfo .weather-icon img{width:50px;height:auto}.weatherInfo .weather-details .weather-main{text-transform:uppercase}.weatherInfo .weather-details .weather-temperature:after{content:"℉"}@media(min-width: 768px){.weather-wrap{font-size:75%}}